import React from 'react'
import { useTranslation } from 'react-i18next'
import { useUser } from '../context/UserContext'
import { Download, Share2, Mail } from 'lucide-react'

// Utilitaire pour gnrer certificat SVG/PDF
const generateCertificateHTML = (userName, courseName, completionDate, points, percentage) => {
  const date = new Date(completionDate).toLocaleDateString('en-US', {
    year: 'numeric',
    month: 'long',
    day: 'numeric'
  })

  return `
    <html>
      <head>
        <style>
          @page {
            margin: 0;
          }
          body {
            margin: 0;
            padding: 0;
            font-family: 'Georgia', serif;
            background: white;
          }
          .certificate {
            width: 1200px;
            height: 800px;
            padding: 60px;
            box-sizing: border-box;
            background: linear-gradient(135deg, #f0f4f8 0%, #d9e2ec 100%);
            border: 8px solid #2ecc71;
            position: relative;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            align-items: center;
            text-align: center;
          }
          .certificate::before {
            content: '';
            position: absolute;
            top: 20px;
            left: 20px;
            right: 20px;
            bottom: 20px;
            border: 2px solid #2ecc71;
            opacity: 0.3;
          }
          .content {
            position: relative;
            z-index: 1;
          }
          .header {
            font-size: 48px;
            font-weight: bold;
            color: #27ae60;
            margin-bottom: 20px;
            text-transform: uppercase;
            letter-spacing: 2px;
          }
          .subtitle {
            font-size: 24px;
            color: #2c3e50;
            margin-bottom: 40px;
            font-style: italic;
          }
          .recipient {
            font-size: 32px;
            font-weight: bold;
            color: #34495e;
            margin: 30px 0;
            text-decoration: underline;
            text-decoration-style: dotted;
          }
          .course-name {
            font-size: 28px;
            color: #2ecc71;
            margin: 30px 0;
            font-weight: bold;
          }
          .details {
            font-size: 16px;
            color: #555;
            margin: 30px 0;
            line-height: 1.8;
          }
          .achievement {
            font-size: 18px;
            color: #27ae60;
            font-weight: bold;
            margin: 20px 0;
            background: rgba(46, 204, 113, 0.1);
            padding: 15px;
            border-radius: 5px;
          }
          .footer {
            font-size: 14px;
            color: #7f8c8d;
            margin-top: 40px;
            padding-top: 20px;
            border-top: 1px solid #bdc3c7;
          }
          .seal {
            font-size: 60px;
            margin: 20px 0;
          }
        </style>
      </head>
      <body>
        <div class="certificate">
          <div class="content">
            <div class="seal"></div>
            <div class="header">Certificate of Completion</div>
            <div class="subtitle">SafeFood NYC Academy</div>
            
            <div style="margin: 40px 0;">
              <div style="font-size: 18px; color: #555;">This is to certify that</div>
              <div class="recipient">${userName}</div>
              <div style="font-size: 18px; color: #555;">has successfully completed the course</div>
            </div>

            <div class="course-name">${courseName}</div>

            <div class="achievement">
              Achieved ${percentage}% with ${points} points 
            </div>

            <div class="details">
              Demonstrating competency in food safety best practices<br>
              and professional standards for restaurant and hotel operations
            </div>

            <div class="details">
              Completed on: <strong>${date}</strong>
            </div>

            <div class="footer">
              <div>SafeFood NYC Academy</div>
              <div>Professional Food Safety Certification</div>
              <div>Certificate ID: SFOOD-${Date.now()}</div>
            </div>
          </div>
        </div>
      </body>
    </html>
  `
}

export default function Certificates() {
  const { t } = useTranslation()
  const { user, userProgress } = useUser()
  const [selectedCertificate, setSelectedCertificate] = React.useState(null)

  React.useEffect(() => {
    if (!user) {
      window.location.href = '/auth'
    }
  }, [user])

  if (!user) return null

  // Construire la liste des certificats base sur les quizzes complts
  const courses = [
    { id: 1, name: 'Basic Food Hygiene', lessons: [1, 2, 3, 4, 5] },
    { id: 2, name: 'Advanced Sanitation', lessons: [1, 2, 3, 4, 5] },
    { id: 3, name: 'Allergen Management', lessons: [1, 2, 3, 4] },
  ]

  const certificates = courses
    .map(course => {
      const lessonsCompleted = course.lessons.filter(lessonId => {
        const quizKey = `${course.id}-${lessonId}`
        return userProgress.quizScores?.[quizKey]
      })

      if (lessonsCompleted.length === 0) return null

      const scores = lessonsCompleted.map(id => userProgress.quizScores?.[`${course.id}-${id}`]?.percentage || 0)
      const avgPercentage = Math.round(scores.reduce((a, b) => a + b, 0) / scores.length)
      const totalPoints = lessonsCompleted.length * 100

      return {
        courseId: course.id,
        courseName: course.name,
        completionDate: userProgress.quizScores?.[`${course.id}-${lessonsCompleted[lessonsCompleted.length - 1]}`]?.completedAt,
        averagePercentage: avgPercentage,
        points: totalPoints,
        lessonsCount: lessonsCompleted.length,
      }
    })
    .filter(Boolean)

  const downloadCertificate = (cert) => {
    const html = generateCertificateHTML(
      user.fullName,
      cert.courseName,
      cert.completionDate,
      cert.points,
      cert.averagePercentage
    )

    // Crer un lment invisible pour gnrer le PDF
    const element = document.createElement('div')
    element.innerHTML = html
    element.style.display = 'none'
    document.body.appendChild(element)

    // Utiliser print pour gnrer PDF
    const printWindow = window.open('', '', 'width=1200,height=800')
    printWindow.document.write(html)
    printWindow.document.close()
    printWindow.print()

    document.body.removeChild(element)
  }

  const shareCertificate = (cert) => {
    const text = `I just completed "${cert.courseName}" on SafeFood NYC Academy with ${cert.averagePercentage}%!  Professional food safety certification for restaurants and hotels.`
    
    if (navigator.share) {
      navigator.share({
        title: 'SafeFood NYC Academy Certificate',
        text: text,
        url: 'https://sowk78824-eng.github.io/safefood-nyc-academy'
      })
    } else {
      // Fallback: copier le texte
      navigator.clipboard.writeText(text)
      alert('Certificate info copied to clipboard!')
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-primary to-secondary p-4 md:p-8">
      <div className="max-w-5xl mx-auto">
        {/* Header */}
        <div className="text-center mb-12">
          <h1 className="text-4xl font-bold text-white mb-4">Your Certificates</h1>
          <p className="text-gray-100 text-lg">
            Professional certifications for your food safety training
          </p>
        </div>

        {certificates.length === 0 ? (
          <div className="bg-white rounded-lg shadow-lg p-8 text-center">
            <p className="text-gray-600 text-lg mb-4">
              No certificates yet. Complete a course to earn your certificate!
            </p>
            <a href="/academy" className="text-primary font-bold hover:underline">
              Go to Academy 
            </a>
          </div>
        ) : (
          <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
            {certificates.map((cert, idx) => (
              <div key={idx} className="bg-white rounded-lg shadow-lg overflow-hidden hover:shadow-xl transition">
                {/* Card Header */}
                <div className="bg-gradient-to-r from-primary to-secondary p-6 text-white">
                  <h3 className="text-2xl font-bold mb-2">{cert.courseName}</h3>
                  <p className="text-gray-100">{cert.lessonsCount} lessons completed</p>
                </div>

                {/* Card Body */}
                <div className="p-6 space-y-4">
                  {/* Score Display */}
                  <div className="flex justify-around items-center">
                    <div className="text-center">
                      <p className="text-gray-600 text-sm font-semibold">AVERAGE SCORE</p>
                      <p className="text-4xl font-bold text-secondary">{cert.averagePercentage}%</p>
                    </div>
                    <div className="text-center">
                      <p className="text-gray-600 text-sm font-semibold">POINTS EARNED</p>
                      <p className="text-4xl font-bold text-primary">{cert.points}</p>
                    </div>
                  </div>

                  {/* Certificate Preview */}
                  <div 
                    className="bg-gray-50 p-4 rounded border-2 border-dashed border-primary cursor-pointer hover:bg-gray-100 transition"
                    onClick={() => setSelectedCertificate(cert)}
                  >
                    <div className="text-center">
                      <p className="text-5xl mb-2"></p>
                      <p className="text-gray-700 font-semibold">Click to Preview</p>
                      <p className="text-gray-600 text-sm">Certificate of Completion</p>
                    </div>
                  </div>

                  {/* Action Buttons */}
                  <div className="grid grid-cols-2 gap-3 pt-4">
                    <button
                      onClick={() => downloadCertificate(cert)}
                      className="flex items-center justify-center gap-2 bg-secondary text-white py-3 rounded font-semibold hover:bg-primary transition"
                    >
                      <Download size={18} />
                      Download PDF
                    </button>
                    <button
                      onClick={() => shareCertificate(cert)}
                      className="flex items-center justify-center gap-2 bg-blue-500 text-white py-3 rounded font-semibold hover:bg-blue-600 transition"
                    >
                      <Share2 size={18} />
                      Share
                    </button>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}

        {/* Certificate Preview Modal */}
        {selectedCertificate && (
          <div className="fixed inset-0 bg-black bg-opacity-50 z-50 flex items-center justify-center p-4 overflow-y-auto">
            <div className="bg-white rounded-lg shadow-2xl max-w-4xl w-full my-8">
              <div className="p-4 flex justify-between items-center border-b">
                <h2 className="text-2xl font-bold">Certificate Preview</h2>
                <button
                  onClick={() => setSelectedCertificate(null)}
                  className="text-gray-600 hover:text-gray-800 text-2xl"
                >
                  
                </button>
              </div>
              
              <div className="p-8 bg-gray-100">
                <div 
                  dangerouslySetInnerHTML={{
                    __html: generateCertificateHTML(
                      user.fullName,
                      selectedCertificate.courseName,
                      selectedCertificate.completionDate,
                      selectedCertificate.points,
                      selectedCertificate.averagePercentage
                    )
                  }}
                />
              </div>

              <div className="p-4 border-t flex gap-3">
                <button
                  onClick={() => downloadCertificate(selectedCertificate)}
                  className="flex-1 flex items-center justify-center gap-2 bg-secondary text-white py-3 rounded font-semibold hover:bg-primary transition"
                >
                  <Download size={18} />
                  Download as PDF
                </button>
                <button
                  onClick={() => setSelectedCertificate(null)}
                  className="flex-1 bg-gray-300 text-gray-700 py-3 rounded font-semibold hover:bg-gray-400 transition"
                >
                  Close
                </button>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  )
}
